<template>
  <div class="search">
    <el-input placeholder="请输入内容" v-model="keyword"
     prefix-icon="el-icon-search" 
     clearable v-on:keyup.native.enter="getEnter"></el-input>
    <div class="list">
      <ul v-if="islistShow">
        <li v-for="(item, index) in keywordList" :key="index" @click="getsong(item.keyword)">
          <i class="el-icon-search"></i>
          <span>{{item.keyword}}</span>
        </li>
      </ul>
    </div>
    <div class="hot-search" v-if="isbuttonShow">
      <h3>热门搜索</h3>
      <el-button
        size="small"
        round
        v-for="(item, index) in hotSearch"
        :key="index"
        @click="getsong(item.first)"
      >{{item.first}}</el-button>
    </div>
    <div class="bestsong" v-if="bestsong">
      <div class="bestmv" v-if="bestmv[0]">
        <img :src="bestmv[0].cover" alt="" srcset="">
        <p><span>Mv：</span><span>{{bestmv[0].name}}</span></p>
      </div>
      <div class="bestvideo" v-if="!bestmv&&bestvideo[0]">
          <img :src="bestvideo[0].coverUrl" alt="" srcset="">
          <p><span>Video：</span><span>{{bestvideo[0].name}}</span></p>
      </div>
      <div class="bestartist" v-if="bestartist[0]">
          <img :src="bestartist[0].picUrl" alt="" srcset="">
          <p><span>歌手：</span><span>{{bestartist[0].name}}</span></p>
      </div>
    </div>
    <div class="songlist">
      <SongList3
        v-for="(item, index) in list"
        :key="index"
        :item="item"
       
        :list="list"
      ></SongList3>
    </div>
  </div>
</template>
<style lang="less" >
.search {
  .el-input {
    padding: 15px 10px;

    box-sizing: border-box;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    input {
      border-radius: 50px;
    }
  }
  .list {
    position: relative;

    ul {
      position: absolute;
      top: 0;
      left: 0;
      background: white;
      width: 100%;
      margin: 0;
      li {
        display: flex;
        align-items: center;
        height: 45px;
        padding-left: 10px;
        box-sizing: border-box;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        position: relative;
        i {
          position: absolute;
          top: 14px;
          left: -29px;
          color: rgba(0, 0, 0, 0.1);
        }
      }
    }
  }
  .hot-search {
    box-sizing: border-box;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    padding: 15px 10px 0;
    h3 {
      font-size: 12px;
      line-height: 12px;
      color: #666;
    }
    .el-button {
      margin: 3px;
    }
  }
  .bestsong{
    &>div{
      padding: 5px 10px;
      width: 100%;
      box-sizing: border-box;
      overflow: hidden;
        img{
          float: left;
      width: 100px;
      height: 50px;
    }
    p{
      float: left;
    }
    }
    
  }
}
</style>

<script>
import SongList3 from "@/components/SongList3.vue";
export default {
   components:{
    SongList3
  },
  data() {
    return {
      keyword: null,
      hotSearch: [],
      keywordList: [],
      islistShow: false,
      isbuttonShow: true,
      isclick: false,
      bestsong:null,
      bestmv:[],
      bestvideo:[],
      bestartist:[],
      tracks:null,
      list:null,
    };
  },
  watch: {
    keyword: function(newValue, oldValue) {
      if (newValue) {
        if (!this.isclick) {
          this.islistShow = true;
          this.getSuggestSearch();
          this.isbuttonShow = false;
        } else {
          this.islistShow = false;
          this.isbuttonShow = false;
        }
      } else {
        this.islistShow = false;
        this.isbuttonShow = true;
        this.keywordList = [];
      }
      this.isclick = false;
      // console.log(newValue,this.isclick);
    }
  },
  methods: {
    getAxios() {
      let that = this;
      axios
        .get("/search/hot")
        .then(function(response) {
          that.hotSearch = response.data.result.hots;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getSuggestSearch() {
      let that = this;
      axios
        .get("/search/suggest", {
          params: {
            keywords: this.keyword,
            type: "mobile"
          }
        })
        .then(function(response) {
          that.keywordList = response.data.result.allMatch;
          
        })
        .catch(function(error) {
          console.log(error);
        });
    },
     //获取最佳结果
    getbestsong(){
       let that = this;
     
      axios
        .get("/search/multimatch", {
          params: {
            keywords: this.keyword,
           
          }
        })
        .then(function(response) {
         
         
          that.bestsong=response.data.result;
          if (response.data.result.mv) {
            that.bestmv=response.data.result.mv;
          }
          if (response.data.result.video) {
            that.bestvideo=response.data.result.video;
          }
          if (response.data.result.artist) {
            that.bestartist=response.data.result.artist;
          }
          
         
           console.log(that.bestsong);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getsong(item) {
      this.keyword = item;
      this.keywordList = [];
      this.isclick = true;
      this.isbuttonShow=false;
      this.getbestsong()

      let that = this;
     
      axios
        .get("/search", {
          params: {
            keywords:item,
           
          }
        })
        .then(function(response) {
          // that.keywordList = response.data.result.allMatch;
          // console.log(response.data.result.songs);
          that.list=response.data.result.songs
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getEnter(){
       this.getsong(this.keyword)
      this.isclick=false;
      this.isbuttonShow=false;
    }
  },
  created() {
    this.getAxios();
    
  }
};
</script>